<template>
  <div id="app" v-bind:class="{ modeOn: night_mode }" style="height: 100vh;">
    <header>
        <router-link :to="{ name: 'home'}">Welcome to my page, Have a nice day</router-link>
        <div class="switch-div">
          <label class="switch">
            <input type="checkbox" v-on:click="changeMode" v-model="night_mode">
            <span class="slider round"></span>
          </label>
        </div>
    </header>
    <main>
      <img style="width: 25%" src="./assets/logo.png" alt="Hello guys Logo">
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      color_: '#fff',
      night_mode: false,
    };
  },
  methods: {
    changeMode() {
      console.log(this.night_mode);
      if (this.night_mode === true) {
        this.night_mode = false;
      } else {
        this.night_mode = true;
      }
    },
  },
  watch: {
    night_mode() {
      if (this.night_mode === true) {
        console.log('night_mode_on');
      } else {
        console.log('night_mode_off');
      }
    },
  },
};
</script>

<style scoped>

.modeOn {
  background-color: #35495E;
}

.switch-div {
  right: 20px; 
  padding-top: 20px; 
  position: absolute; 
  display: inline-block
}

.switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 17px;
}

.switch input {display:none;}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 13px;
  width: 13px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #35495E;
}

input:focus + .slider {
  box-shadow: 0 0 1px #35495E;
}

input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 17px;
}

.slider.round:before {
  border-radius: 50%;
}

</style>

<style lang="less" src="./assets/styles/app.less"></style>



